import React, { Component } from "react";

class Cabecalho extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuAberto: true,
      paginaAtual: props.paginaAtual,
      librasAtivo: false,
    };

    this.interagirMenu = this.interagirMenu.bind(this);
    this.iconeMenu = this.iconeMenu.bind(this);
    this.mudarOpcaoLibras = this.mudarOpcaoLibras.bind(this);
  }

  interagirMenu(event) {
    if (document.getElementById("sidebar").classList.contains("active")) {
      document.getElementById("sidebar").classList.remove("active");
    } else {
      document.getElementById("sidebar").classList.add("active");
    }
    this.setState({ menuAberto: !this.state.menuAberto });
  }

  iconeMenu() {
    if (!this.state.menuAberto) {
      return <i className="fa fa-bars fa-lg"></i>;
    }
    return <i className="fa fa-times fa-lg"></i>;
  }

  mudarOpcaoLibras(event) {
    this.setState({ librasAtivo: !this.state.librasAtivo });
  }

  render() {
    return (
      <div id="cabecalho" className="w-100 p-3 bg-light m-0 ">
        <button
          type="button"
          title={
            !this.state.menuAberto
              ? "Abrir menu lateral"
              : "Fechar menu lateral"
          }
          className="btn btn-primary"
          onClick={this.interagirMenu}
        >
          {this.iconeMenu()}
        </button>
        <div id="acoesUsuario" className="float-right">
          <button
            type="button"
            title={
              this.state.librasAtivo
                ? "Destivar acessibilidade em Libras"
                : "Ativar acessibilidade em Libras"
            }
            className={
              this.state.librasAtivo
                ? "btn btn-dark-blue mr-2 text-white"
                : "btn mr-2 text-dark"
            }
            onClick={this.mudarOpcaoLibras}
          >
            <i className="fa fa-sign-language fa-lg"></i>
          </button>
          <button
            type="button"
            title="Ver Notificações"
            className="btn btn-danger mr-2"
          >
            <i className="fa fa-bell fa-lg"></i>
          </button>
          <button type="button" className="btn btn-primary" title="Entrar">
            <i className="fa fa-sign-in-alt fa-lg"></i> Entrar
          </button>
        </div>
      </div>
    );
  }
}

export default Cabecalho;
