import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import "./MenuLateral.css";

class MenuLateral extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuAberto: true,
      paginaAtual: props.paginaAtual,
      librasAtivo: false,
    };

    this.interagirMenu = this.interagirMenu.bind(this);
    this.iconeMenu = this.iconeMenu.bind(this);
  }

  interagirMenu(event) {
    if (document.getElementById("sidebar").classList.contains("active")) {
      document.getElementById("sidebar").classList.remove("ative");
    } else {
      document.getElementById("sidebar").classList.add("active");
    }
    this.setState({ menuAberto: !this.state.menuAberto });
  }

  iconeMenu() {
    if (!this.state.menuAberto) {
      return <i className="fa fa-bars fa-lg"></i>;
    }
    return <i className="fa fa-times fa-lg"></i>;
  }

  render() {
    return (
      <nav id="sidebar" className="bg-dark">
        <div id="cabecalhoMenuLateral" className="w-100 p-3 m-0 ">
          <button
            type="button"
            title={
              !this.state.menuAberto
                ? "Abrir menu lateral"
                : "Fechar menu lateral"
            }
            className="btn btn-primary"
            onClick={this.interagirMenu}
          >
            {this.iconeMenu()}
          </button>
        </div>
        <div className="sidebar-header">
          <h2>OBA</h2>
        </div>
        <ul className="list-unstyled components">
          <li>
            <NavLink to="/">
              <i className="fa fa-home fa-lg ml-1 mr-1"></i>Inicio
              <i className="pt-1 float-right fa fa-angle-left fa-xl"></i>
            </NavLink>
          </li>
          <li>
            <NavLink to="/sobre">
              <i className="fa fa-info fa-lg ml-1 mr-1"></i> Sobre
              <i className="pt-1 float-right fa fa-angle-left fa-xl"></i>
            </NavLink>
          </li>
          <li>
            <NavLink to="/equipe">
              <i className="fa fa-user-friends fa-lg ml-1 mr-1"></i> Equipe
              <i className="pt-1 float-right fa fa-angle-left fa-xl"></i>
            </NavLink>
          </li>
          <li>
            <NavLink to="/publicacoes">
              <i className="fa fa-journal-whills fa-lg ml-1 mr-1"></i>
              <i className="pt-1 float-right fa fa-angle-left fa-xl"></i>
              Publicações
            </NavLink>
          </li>
          <li>
            <NavLink to="/manual">
              <i className="fa fa-book fa-lg ml-1 mr-1"></i> Manual
              <i className="pt-1 float-right fa fa-angle-left fa-xl"></i>
            </NavLink>
          </li>
          {/* <li>
            <a
              href="#homeSubmenu"
              data-toggle="collapse"
              aria-expanded="false"
              className="dropdown-toggle"
            >
              Home
            </a>
            <ul className="collapse list-unstyled" id="homeSubmenu">
              <li>
                <a href="#">Home 1</a>
              </li>
              <li>
                <a href="#">Home 2</a>
              </li>
              <li>
                <a href="#">Home 3</a>
              </li>
            </ul>
          </li> */}
        </ul>
      </nav>
    );
  }
}

export default MenuLateral;
