import React, { Component } from "react";
import ListaArtigos from "./ListaArtigos.js";

class ListaTipos extends Component {
  constructor(props) {
    super(props);
    this.state = {
      artigoTipos: this.props.artigoTipos,
    };
  }

  render() {
    return Object.keys(this.state.artigoTipos).map((tipo) => {
      return (
        <div key={tipo}>
          <h6>{tipo}</h6>
          <ListaArtigos artigos={this.state.artigoTipos[tipo]} />
        </div>
      );
    });
  }
}

export default ListaTipos;
