import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "./App.css";

import Cabecalho from "./layout/Cabecalho";
import MenuLateral from "./layout/MenuLateral";
import Inicio from "./paginas/inicio/Inicio";
import Manual from "./paginas/manual/Manual";
import Sobre from "./paginas/sobre/Sobre";
import Equipe from "./paginas/equipe/Equipe";
import Publicacoes from "./paginas/publicacoes/Publicacoes";

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="wrapper">
          <Router>
            <MenuLateral onChange={this.onChange} />
            <div className="container-fluid p-0">
              <Cabecalho />
              <div
                className={
                  window.innerWidth < 414
                    ? "conteudo pt-2 pb-2"
                    : "conteudo pt-4 pb-4"
                }
              >
                <Route exact path="/" component={Inicio} />
                <Route exact path="/sobre" component={Sobre} />
                <Route exact path="/equipe" component={Equipe} />
                <Route exact path="/publicacoes" component={Publicacoes} />
                <Route exact path="/manual" component={Manual} />
              </div>
            </div>
          </Router>
        </div>
      </div>
    );
  }
}

export default App;
