import React, { Component } from "react";
import "./Manual.css";

class CadastroLogin extends Component {
  render() {
    return (
      <div className="mb-3">
        <a
          data-toggle="collapse"
          href="#collapseCadastroLogin"
          role="button"
          aria-expanded="false"
          aria-controls="collapseCadastroLogin"
        >
          <li className="border-none bg-light p-3 shadow text-uppercase">
            <i className="fa fa-exclamation-circle fa-lg mr-2 ml-1 text-primary"></i>
            Cadastro e login
          </li>
        </a>
        <div className="collapse bg-white" id="collapseCadastroLogin">
          <div className="card card-body shadow">
            <div className="row">
              <div className="col-md-12 col-lg-6">
                <p>
                  O cadastro na plataforma é rápido, fácil e totalmente
                  gratuito.
                </p>
                <ol>
                  <b>Para isso, você tem três opções:</b>
                  <li>
                    Você pode utilizar o seu Facebook. Basta clicar, autenticar
                    e pronto! Está cadastrado e logado! Falei que era rápido!?
                  </li>
                  <li>
                    Você pode utilizar, também, o seu Gmail. Basta clicar,
                    autenticar e pronto! Cadastro e login na Plataforma OBAMA
                    realizados com sucesso!
                  </li>
                  <li>
                    A terceira opção é um cadastro manual, onde você deve
                    colocar o seu nome, e-mail e uma senha, depois é só
                    confirmar o e-mail cadastrado. Simples né?
                  </li>
                </ol>
              </div>
              <div className="col-md-12 col-lg-6">
                <h5 className="text-center mt-2">
                  Video explicativo <b>acessível em libras</b>
                </h5>
                <div className="w-100">
                  <iframe
                    title="Vídeo explicativo acessível me libras para fazer o cadastro e o login"
                    className="videoManual"
                    src="https://www.youtube.com/embed/Anx0xQ4dqmA"
                    frameBorder="0"
                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen=""
                  ></iframe>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class BuscaTextual extends Component {
  render() {
    return (
      <div className="mb-3">
        <a
          data-toggle="collapse"
          href="#collapseBuscaTextual"
          role="button"
          aria-expanded="false"
          aria-controls="collapseBuscaTextual"
        >
          <li className="border-none bg-light p-3 shadow text-uppercase">
            <i className="fa fa-exclamation-circle fa-lg mr-2 ml-1 text-primary"></i>
            Busca Textual
          </li>
        </a>
        <div className="collapse bg-white" id="collapseBuscaTextual">
          <div className="card card-body shadow">
            <div className="row">
              <div className="col-md-12 col-lg-6">
                <p>
                  Nessa parte, você pode fazer uma busca textual rápida,
                  colocando alguns termos que estejam relacionados a conteúdos
                  da matemática (você pode usar termos que aparecem nos
                  descritores dos PCN e BNCC!)
                </p>
              </div>
              <div className="col-md-12 col-lg-6">
                <h5 className="text-center mt-2">
                  Video explicativo <b>acessível em libras</b>
                </h5>
                <div className="w-100">
                  <iframe
                    title="Vídeo explicativo acessível me libras para fazer o cadastro e o login"
                    className="videoManual"
                    src="https://www.youtube.com/embed/7RJ7VUaIyEU"
                    frameBorder="0"
                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen=""
                  ></iframe>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class BuscaAvancada extends Component {
  render() {
    return (
      <div className="mb-3">
        <a
          data-toggle="collapse"
          href="#collapseBuscaAvancada"
          role="button"
          aria-expanded="false"
          aria-controls="collapseBuscaAvancada"
        >
          <li className="border-none bg-light p-3 shadow text-uppercase">
            <i className="fa fa-exclamation-circle fa-lg mr-2 ml-1 text-primary"></i>
            Busca Avançada
          </li>
        </a>
        <div className="collapse bg-white" id="collapseBuscaAvancada">
          <div className="card card-body shadow">
            <div className="row">
              <div className="col-md-12 col-lg-6">
                <p>
                  Para pesquisar com mais precisão (e sabedoria), você pode
                  utilizar a busca avançada da plataforma, que te permite achar
                  objetos por:
                </p>
                <ol>
                  <li>Tema curricular;</li>
                  <li>
                    Descritores (utilizados os descritores do Sistema de
                    Avaliação da Educação Básica -Saeb);
                  </li>
                  <li>Nível de ensino;</li>
                  <li>
                    Tipo de visualização (Celular/Tablet, Online ou Download).
                  </li>
                </ol>
              </div>
              <div className="col-md-12 col-lg-6">
                <h5 className="text-center mt-2">
                  Video explicativo <b>acessível em libras</b>
                </h5>
                <div id="videoManual" className="w-100">
                  <iframe
                    title="Vídeo explicativo acessível me libras para fazer o cadastro e o login"
                    className="videoManual"
                    src="https://www.youtube.com/embed/9PsXHtWS-8k"
                    frameBorder="0"
                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen=""
                  ></iframe>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class CriarPlano extends Component {
  render() {
    return (
      <div className="mb-3">
        <a
          data-toggle="collapse"
          href="#collapseCriarPlano"
          role="button"
          aria-expanded="false"
          aria-controls="collapseCriarPlano"
        >
          <li className="border-none bg-light p-3 shadow text-uppercase">
            <i className="fa fa-exclamation-circle fa-lg mr-2 ml-1 text-primary"></i>
            Criação de Plano de Aula
          </li>
        </a>
        <div className="collapse bg-white" id="collapseCriarPlano">
          <div className="card card-body shadow">
            <div className="row">
              <div className="col-md-12 col-lg-6">
                <p>
                  Que tal conhecer nossa ferramenta para escrita de plano de
                  aula? E ainda dá para compartilhar com seus colegas e
                  produzirem de forma colaborativa?
                </p>
                <b>Para isso, você tem três opções:</b>
                <ol>
                  <b>
                    A escrita do plano de aula é dividida em três passos, vamos
                    conhecer cada um desses passos?
                  </b>
                  <li>
                    No <b>primeiro passo</b> você deve informar o nome da
                    instituição a que se destina esse plano de aula, uma frase
                    curta que defina sua aula (esse será o título da aula), a
                    que ano de ensino sua proposta de aula melhor se aplica e
                    quanto tempo (em minutos) você acha necessário para a
                    aplicação deste plano de aula.{" "}
                  </li>
                  <li>
                    O <b>segundo passo</b> é a descrição do seu plano de aula,
                    onde você deve escrever seu objetivo geral, objetivos
                    específicos, metodologia, avaliação, recursos necessários e
                    referências.
                  </li>
                  <li>
                    No <b>terceiro e último passo</b> você pode buscar e
                    adicionar (ou não) um ou mais OA para complementar seu plano
                    de aula e então enviar para revisão onde iremos validar seu
                    plano, por isso, capricha!
                  </li>
                </ol>
              </div>
              <div className="col-md-12 col-lg-6">
                <h5 className="text-center mt-2">
                  Video explicativo <b>acessível em libras</b>
                </h5>
                <div className="w-100">
                  <iframe
                    title="Vídeo explicativo acessível me libras para fazer o cadastro e o login"
                    className="videoManual"
                    src="https://www.youtube.com/embed/NFyNoyrA7CY"
                    frameBorder="0"
                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen=""
                  ></iframe>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class Manual extends Component {
  render() {
    return (
      <div className="text-center mr-auto ml-auto w-95 bg-transparent rounded">
        <h1 className="pt-4">Manual</h1>
        <ul
          id="listaManuais"
          className="list-unstyled border-none text-justify p-3"
        >
          <CadastroLogin />
          <BuscaTextual />
          <BuscaAvancada />
          <CriarPlano />
        </ul>
      </div>
    );
  }
}

export default Manual;
